"use strict";

import ASCIIMonitor from "../lib/ASCIIMonitor";

export default {
  run: () => {
    const monitor = new ASCIIMonitor("test-area", 10, 10, {
      fontFamily: "Lucida Console, Monaco, monospace"
    });

    const player = {
      x: 2,
      y: 2,
      char: "@"
    }
    const blocks = [];

    for (let i = 0 ; i < 10 ; i++) {
      const x = Math.floor(Math.random() * 10);
      const y = Math.floor(Math.random() * 10);
      const char = "#";

      if ([player, ...blocks].some(coords => coords.x === x && coords.y === y)) {
        i--;

        continue;
      }

      blocks.push({ x, y, char });
    }

    const draw = () => {
      monitor.clear();

      monitor.setCharacter(player.x, player.y, player.char, {
        color: "green",
        textShadow: "0 0 25px green"
      });

      for (const obj of blocks) {
        monitor.setCharacter(obj.x, obj.y, obj.char, {
          color: "red",
          textShadow: "0 0 25px red"
        });
      }
    }

    draw();

    const tryMove = (x, y) => {
      const outOfBounds = x < 0 || x > 9 || y < 0 || y > 9;
      const blockOnTheWay = blocks.some(coords => coords.x === x && coords.y === y);

      if (!outOfBounds && !blockOnTheWay) {
        player.x = x;
        player.y = y;

        draw();
      }
    }

    document.onkeydown = function(evt) {
      if (evt.key === "ArrowUp") {
        tryMove(player.x, player.y - 1);
      }
      else if (evt.key === "ArrowDown") {
        tryMove(player.x, player.y + 1);
      }
      else if (evt.key === "ArrowLeft") {
        tryMove(player.x - 1, player.y);
      }
      else if (evt.key === "ArrowRight") {
        tryMove(player.x + 1, player.y);
      }
    }
  }
}