"use strict";

import ASCIIMonitor from "../lib/ASCIIMonitor";

export default {
  run: function() {
    const size = {width: 20, height: 20};
    const monitor = new ASCIIMonitor("test-area", size.width, size.height, {
      fontFamily: "Lucida Console, Monaco, monospace"
    });

    let snake = [{x: 8, y: 10}, {x: 9, y: 10}, {x: 10, y: 10}, {x: 11, y: 10}, {x: 12, y: 10}];
    let direction = "right";
    let newDirection = "right";
    let apple = this.getApple(snake, size);
    let paused = true;
    
    setInterval(() => {
      if (!paused) {
        direction = newDirection;
        let lastIdx = snake.length - 1;
        let head = snake[lastIdx];
        const gotApple = apple.x === head.x && apple.y === head.y;
        monitor.clear();

        if (direction === "up") {
          snake.push({x: head.x, y: head.y - 1});
        }
        else if (direction === "down") {
          snake.push({x: head.x, y: head.y + 1});
        }
        else if (direction === "left") {
          snake.push({x: head.x - 1, y: head.y});
        }
        else if (direction === "right") {
          snake.push({x: head.x + 1, y: head.y});
        }

        if (gotApple) {
          apple = this.getApple(snake, size);
        }
        else {
          snake.shift();
        }

        lastIdx = snake.length - 1;
        head = snake[lastIdx];

        const outOfBounds = head.x < 0 || head.x > size.width - 1 || head.y < 0 || head.y > size.height - 1;
        const selfCollision = snake.slice(0, lastIdx).some(part => part.x === head.x && part.y === head.y);

        if (outOfBounds || selfCollision) {
          direction = "right";
          newDirection = "right";
          snake = [{x: 8, y: 10}, {x: 9, y: 10}, {x: 10, y: 10}, {x: 11, y: 10}, {x: 12, y: 10}];
          apple = this.getApple(snake, size);
          paused = true;
        }
        else {
          monitor.setCharacter(apple.x, apple.y, "o", {
            color: "red"
          });
  
          for (const part of snake) {
            monitor.setCharacter(part.x, part.y, "#", {
              color: "green"
            });
          }
        }

        if (outOfBounds || selfCollision) {
          direction = "right";
          newDirection = "right";
          snake = [{x: 8, y: 10}, {x: 9, y: 10}, {x: 10, y: 10}, {x: 11, y: 10}, {x: 12, y: 10}];
          apple = this.getApple(snake, size);
          paused = true;
        }
      }
      else {
        const str = ["P", "A", "U", "S", "E", "D"];
        monitor.clear();
        for (let i = 0; i < str.length; i++) {
          monitor.setCharacter(5 + i, 5, str[i], {
            color: "white"
          });
        }
      }
    }, 200);
    
    document.onkeydown = function(evt) {
      if (evt.key === "ArrowUp" && direction !== "down") {
        newDirection = "up";
        paused = false;
      }
      else if (evt.key === "ArrowDown" && direction !== "up") {
        newDirection = "down";
        paused = false;
      }
      else if (evt.key === "ArrowLeft" && direction !== "right") {
        newDirection = "left";
        paused = false;
      }
      else if (evt.key === "ArrowRight" && direction !== "left") {
        newDirection = "right";
        paused = false;
      }
      if (evt.key === "p") {
        paused = true;
      }
    }
  },

  getApple: function(snake, size) {
    let x, y;
    do {
      x = Math.floor(Math.random() * size.width);
      y = Math.floor(Math.random() * size.height);

    } while (snake.some(part => part.x === x && part.y === y))

    return { x, y };
  }
}